.coin-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.card{
    width: 1200px;
    text-align: center;
    border-top: 5px solid yellow;
  
}
.coin-row{
    padding-top: 20px;
    transition: 1s;
}
.coin-row:hover{
   
        box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
       
}

.coin{
    display: flex;
    align-items: center;
 
    
}

img{
    padding-right: 20px;
    width: 120px;
}
p{
    padding-right: 50px;
    color: azure;
}

h2{
    margin-left: 300px;
    
}
